.divText, .divText h2{
    box-sizing: border-box;
    padding: 0 0 20px 0;
    margin: 0;
}

.divText{
    border-radius: 40px 40px 0 0;
    background-color: #ffffff;
    border-bottom: 8px solid white;
}

.divText h2{
    padding: 20px 40px 0 40px;
    color: #5f428a;
    font-size: 24px;
    text-align: left;
}

.divText p{
    margin: 0;
    color: #757575;
    padding: 10px 40px 20px 40px;
    font-size: 12px;
    text-align: left;
}

.imgIcon{
    padding-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
}

.imgIcon img{
    height: 30px;
}


@media (min-width: 1280px){
    
    .divText{
        padding-top: 10px;  
        border-radius: 0;
    }

    .divText h2{
        font-size: 30px;
        text-align: left;
        padding-bottom: 20px;
    }

    .divText p{
        font-size: 14px;
    }

    .imgIcon{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 50px;
    }
  }