.form {
    display: flex;
    flex-direction: column;
    background-color: white;
}

.form label {
    box-sizing: border-box;
    display: flex;
    align-items: center;
    padding: 0 40px;
    height: 40px;
    width: auto;
    font-size: 14px;
    text-align: left;
    font-weight: bold;
    color: #3f3f3f;
}


.form select {
    box-sizing: border-box;
    width: 80%;
    margin-left: 40px;
    height: 40px;
    background-color: transparent;
    outline: none;
    border: transparent;
    border-bottom: 1.5px solid #8666b6;
    font-size: 18px;
}


.form input[type=text] {
    margin: 0 0 0 40px;
    box-sizing: border-box;
    width: 80%;
    height: 60px;
    background-color: transparent;
    outline: none;
    border: transparent;
    border-bottom: 1.5px solid #8666b6;
    font-size: 18px;

}

.anexo {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

form input[type=file],
form input[type=date] {
    font-size: 18px;
    margin-top: 10px;
    margin-left: 40px;
    margin-bottom: 5px;
    height: 60px;
    width: 200px;
}

form input[type=submit] {
    border-radius: 6px;
    margin: 10px 0 40px 0;
    color: white;
    box-sizing: border-box;
    width: 200px;
    height: 60px;
    background-color: #5f428a;
    outline: none;
    border: transparent;
    font-size: 18px;

}

form input[type=submit]:hover{
    cursor: pointer;
}

.divSubmit {
    display: flex;
    justify-content: center;
    align-items: center;
}

.warning {
    color: #5f428a;
    font-size: 12px;
    padding: 0 20px;
    margin-bottom: 10px;
}



::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    font-size: 16px;
    color: #c0c0c0;
    opacity: 1;
    /* Firefox */
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #c0c0c0;
    font-size: 16px;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: #c0c0c0;
    font-size: 16px;
}



@media (min-width: 1280px) {
    .form {
        align-items: center;
    }

    .form label {
        font-size: 12px;
        padding: 0;
        box-sizing: border-box;
        width: max-content;
        text-align: left;
        justify-content: left;
    }

    .form input[type="text"] {
        font-size: 14px;
        padding: 0;
        margin: 0;
        width: 200px;
    }

    .form select {
        font-size: 14px;
        padding: 0;
        margin: 0;
        width: 200px;
    }

    .labelAndInput {
        height: 80px;
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        width: max-content;
    }

    .duoField {
        padding: 16px;
        display: flex;
        justify-content: space-around;
        align-items: flex-end;
        width: 100%;
        background-color: #f8f8f8;
        margin-bottom: 10px;
        border-radius: 5px;
    }

    .generalContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    form input[type=file],
    form input[type=date] {
        margin: 10px 0 5px 0;
        font-size: 16px;
        height: 60px;
        width: 200px;
    }

    ::placeholder {
        /* Chrome, Firefox, Opera, Safari 10.1+ */
        padding-left: 4px;
        font-size: 14px;
        color: #c0c0c0;
        opacity: 1;
        /* Firefox */
    }
    
    :-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: #c0c0c0;
        font-size: 14px;
    }
    
    ::-ms-input-placeholder {
        /* Microsoft Edge */
        color: #c0c0c0;
        font-size: 14px;
    }

}