.header{
    width: 100%;
    background-color: #A385CF;
}

.icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.icon img{
    position: fixed;
    width: 30px;
}

h2{
    margin: 0;
    text-align: left;
    font-size: 60px;
    padding: 0 80px;
}

.geralContainer{
    height: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: center;
}
